<template>
  <div id="contentPage" class="view-con" style="flex: 1;">
      <div class="nav_left2">
      <div class="title1">
        <div>项目名称 (0人)</div>
        <span>刷新</span>
      </div>
      <div class="title1 title2">
        <div style=" font-weight: bold">人员分组 (7个/暂无)</div>
        <el-button size="small" type="success">新建分组</el-button>
      </div>
      <div v-show="addGroup" class="addGroup">
        <!--<el-input v-model="input" placeholder="请输入内容"></el-input>-->
        <span>&&</span>
      </div>
      <div class="tree-list">
        <el-tree
          :data="data2"
          show-checkbox
          node-key="id"
          :default-expanded-keys="[2, 3]"
          :default-checked-keys="[5]"
          :props="defaultProps">
        </el-tree>
      </div>
      <div class="edit-con">
        <el-button size="mini" type="warning">删除</el-button>
        <el-button class="edit" size="mini" type="success">编辑</el-button>
        <el-button class="bindPerson" size="mini" type="success">关联人</el-button>
      </div>
    </div>
    <div class="main_content">
      <router-view></router-view>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        addGroup: false,
        data2: [{
          id: 1,
          label: '一级 1',
          children: [{
            id: 4,
            label: '二级 1-1',
            children: [{
              id: 9,
              label: '三级 1-1-1'
            }, {
              id: 10,
              label: '三级 1-1-2'
            }]
          }]
        }, {
          id: 2,
          label: '一级 2',
          children: [{
            id: 5,
            label: '二级 2-1'
          }, {
            id: 6,
            label: '二级 2-2'
          }]
        }, {
          id: 3,
          label: '一级 3',
          children: [{
            id: 7,
            label: '二级 3-1'
          }, {
            id: 8,
            label: '二级 3-2'
          }]
        }],
        defaultProps: {
          children: 'children',
          label: 'label'
        }
      }
    },
    created() {
    },
    methods: {}
  }
</script>

<style lang="less" scoped>
  @import (reference) "../../style/mixin";

  .view-con {
    height: 100%;
    .flex()
  }

  #contentPage {
    .nav_left2 {
      width: 252px;
      height: 100%;
      background-color: #fff;
      border-right: 1px solid #DCE4E6;
      display: flex;
      flex-direction: column;
      .title1 {
        background-color: #EFF4F5;
        height: 41px;
        line-height: 41px;
        padding-left: 10px;
        margin-top: 6px;
        .font(14px, #242424);
        .flex(@justify-content: space-between);
        span {
          .font(12px, #30D179);
          padding-right: 10px;
          cursor: pointer;
        }
      }
      .title2{
        background-color: transparent;
        .font(14px, #060606);
        font-weight: bold!important;
        button{
          background-color: #38E275;
          border: 0;
          margin-right: 10px;
        }
        button:hover{
          background-color: #2DCB65;
        }
      }
      .addGroup{
        .flex();
      }
      .tree-list{
        flex: 1;
        overflow: auto;
        .el-tree{
        }
      }
      .edit-con{
        height: 120px;
        border-top: 1px solid #ccc;
        padding-top: 15px;
        .edit{
          background-color: #47E0B3;
          border-color: #47E0B3;
        }
        .bindPerson{
          background-color: #2DCB65;
          border-color: #2DCB65;

        }
      }
    }
    .main_content {
      flex: 1;
      height: 100%;
      background-color: #fff;
    }
  }

</style>



